export default {
    data() {
        return {
            sales: []
        }
    },
    created() {
        // 页面进来时, 根据接口获取特价机票
        this.$axios({
            url: '/airs/sale'
        }).then(res=>{
            // console.log(res.data);
            this.sales = res.data.data
        })
    }
};
